<script>
import {gsap} from "gsap/dist/gsap";

export default {
  name: "Preloader"
}

window.onload = function () {
  document.body.classList.add('preloaded');
  let tl = gsap.timeline();
  tl.from('.preloader__image path', {x: 15, stagger: 0.03, duration: 0.8})
  tl.call(backOut)
  tl.play()
  function backOut() {
    tl.reverse();
  }
  window.setTimeout(function () {
    document.body.classList.add('preloaded_hiding');
    document.body.classList.remove('preloaded');

  }, 3000);
}



</script>

<style lang="scss" scoped>
.preloader {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  background: $color-black;;
  z-index: 1005;
  display: none;
  &__image {
    position: relative;
    top: 50%;
    left: 50%;
    width: 323px;
    height: 183px;
    margin-top: -95px;
    margin-left: -160px;
    text-align: center;
    overflow: visible;
    @media (max-width: $breakpoints-max-desktop) {
      width: 230px;
